
//  header 
const AccountKey = 'Authorization'

//  header 
const UserInfoKey = 'UserInfo'

//token
function getToken() {
  return get(AccountKey)
}


//token
function setToken(token) {
  set(AccountKey, 'Bearer ' + token)
}

//token
function removeToken() {
  return remove(AccountKey)
}

//
function getUserInfo() {
  if (get(UserInfoKey))
    return JSON.stringify(get(UserInfoKey))
  return undefined
}


//
function setUserInfo(userInfo) {
  set(UserInfoKey, JSON.stringify(userInfo))
}

//
function removeUserInfo() {
  return remove(UserInfoKey)
}


function get(key) {
  return localStorage.getItem(key)
}

function set(key, value) {
  return localStorage.setItem(key, value)
}

function remove(key) {
  return localStorage.removeItem(key)
}

export default {
  getToken,
  setToken,
  removeToken,
  getUserInfo,
  setUserInfo,
  removeUserInfo,
  AccountKey,
  UserInfoKey
}




